.task {
  padding:10px 10px 38px 34px;
  height: auto; 
  margin: auto;
  margin-bottom: 8px;
  width: 60%;
  font-family: monospace;
  font-size: 1.4em;
  color: #222;
  background-color:#bdbab7; 
  border-radius: 3px;
  opacity: 1;
}

@keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
@keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }

.fade-in {
    opacity:0;
    animation:fadeIn ease-in 1;
    animation-fill-mode:forwards;
    animation-duration: 0.1s;
}

.taskContent{
  float:left;
  padding-right: 70px;
  padding-bottom: 10px;
  display: block;
}

.closebtn {
  color:#949290;
  font-weight: bold;
  float: right;
  cursor: pointer;
  font-size: 1.3em;
}

.closebtn:hover {
  color: #AC2500;
}
