html{
    height: 100%;
  }

  body {
    width: 100%;
    min-height:100%;
    background-attachment: fixed;
    background-size: cover; 
    background:url('./Static/img/unsplashWhite.webp');
    background-size: cover; 
  }

  * {
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
  }

.taskWrapper, .taskHeader, .taskBody{
  display: flex;
}

.taskWrapper {
    flex: 1 1 auto;
    flex-direction: column;
    height: 100%;
  }
  
  .taskHeader {
    flex: 1 1 5%;
    min-height: 2.5em;
    max-height: 2em;
    font-size: 3.2em;
    color: #bdbab7;
    font-family: monospace;
    text-align: center;
    align-content: center;
    padding:20px 0px 20px 0px;
    margin-bottom: 6px;
    background-color: rgba(0,0,0,0.5);
    text-shadow: 3px 3px 10px #000;
    display: block;
  }

  .taskBody {
    padding-top: 24px;
    flex: 1 1 85%;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    overflow: auto;
  }
  
  .footer {
    display: flex;
    flex: 1 1 5%;    
  }

