.taskWrapper{
    margin: auto;
}

.taskButton, .taskInput{
    width: 50%;
    height:100px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-size: 1.3em;
    font-family: monospace;
    background-color:#bdbab7;
    color: #222;
    transition: 0.2s ease-out;
}

.taskButton{
    margin:0px;
    padding: 8px 24px;
    background-color: rgba(0,0,0,0.5);
    text-shadow: 3px 3px 10px #000;
}

.taskButton:hover{
    background-color: #d0d4d3;
    color: rgb(0, 0, 0);
}

.taskInput{
    padding-left: 20px;
}

.taskInput:focus{
    background-color: #d0d4d3;
    color: #111;
    transition: 0.2s ease-in;
}
